/* Estilos Generales para ManageContacts */
.ManageContacts {
    padding: 2rem;
    max-width: 1000px;
    margin: 2rem auto;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.ManageContacts h2 {
    color: #32325d;
    text-align: center;
    margin-bottom: 1.5rem;
}

/* Estilos para Tabla */
.ManageContacts table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.ManageContacts th,
.ManageContacts td {
    text-align: left;
    padding: 12px;
    border-bottom: 1px solid #ddd;
}

.ManageContacts th {
    background-color: #f4f4f4;
    color: #333;
    font-weight: 600;
}

.ManageContacts tr:nth-child(even) {
    background-color: #f9f9f9;
}

.ManageContacts tr:hover {
    background-color: #f1f1f1;
}

/* Estilos para botones y links */
.ManageContacts .btn {
    padding: 8px 16px;
    text-decoration: none;
    display: inline-block;
    border-radius: 4px;
    transition: background-color 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ManageContacts .secondary__btn {
    background-color: #3498db;
    color: white;
    border: none;
    cursor: pointer;
}

.ManageContacts .secondary__btn:hover {
    background-color: #2980b9;
}

/* Estilos para la carga y mensajes */
.ManageContacts .loading,
.ManageContacts .no-contacts {
    text-align: center;
    color: #666;
    font-size: 1rem;
}

/* Checkbox Estilos */
.ManageContacts input[type="checkbox"] {
    accent-color: #3498db;
}

/* Media queries para responsividad */
@media (max-width: 768px) {
    .ManageContacts table {
        display: block;
        overflow-x: auto;
        white-space: nowrap;
    }

    .ManageContacts th,
    .ManageContacts td {
        padding: 8px;
    }
}