.container {
  margin: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
}

th,
td {
  padding: 12px 16px;
  border: 1px solid #ccc;
  text-align: center;
}

th {
  background-color: #f2f2f2;
  color: #333;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

tr:hover {
  background-color: #f1f1f1;
  cursor: pointer;
}

.actions {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.actions a,
.actions button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  color: #fff;
  text-decoration: none;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 14px;
  transition: box-shadow 0.3s ease-in-out, background-color 0.3s;
}

.actions a.edit,
.btn.edit {
  background-color: #3498db;
  /* Azul */
}

.actions button.delete,
.btn.delete {
  background-color: #e74c3c;
  /* Rojo */
}

.actions a.view,
.btn.view {
  background-color: #27ae60;
  /* Verde */
}

.actions a:hover,
.actions button:hover,
.btn.edit:hover,
.btn.delete:hover,
.btn.view:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.btn {
  display: inline-block;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
}

.btn.secondary__btn {
  background-color: #72a4f5;
  color: #fff;
  border: none;
}

.btn.secondary__btn:hover {
  background-color: #357ae8;
}

.btn.secondary__btn:active {
  background-color: #2a65ca;
}

.btn.secondary__btn:focus {
  outline: none;
  border: 2px solid #e5e5e5;
}