.tour-detail {
    padding: 2rem;
    border: 1px solid #e1e5ee;
    border-radius: 12px;
    margin: 2rem auto;
    background-color: #ffffff;
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    width: 90%;
    max-width: 1100px;

    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.tour-detail h2 {
    color: #32325d;
    font-size: 2.25rem;
    margin-bottom: 0.5rem;
    text-align: center;
}

.tour-detail__info {
    font-size: 1rem;
    color: #525f7f;
    margin: 0.5rem 0;
}

.tour-detail__info-label {
    display: block;
    font-weight: 700;
    margin-bottom: 0.25rem;
}

.tour-images {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 1.5rem;
}

.tour-image {
    border-radius: 8px;
    overflow: hidden;
    transition: all 0.3s;
}

.tour-image:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
}

.back-button {
    padding: 0.75rem 2rem;
    background-color: #625aff;
    color: #ffffff;
    text-decoration: none;
    border-radius: 8px;
    text-align: center;
    font-size: 1rem;
    font-weight: 600;
    margin-top: 1rem;
    align-self: center;
    transition: background-color 0.3s ease;
}

.back-button:hover {
    background-color: #4e4edd;
}

@media (max-width: 768px) {
    .tour-detail {
        padding: 1rem;
    }

    .tour-detail h2 {
        font-size: 1.75rem;
    }

    .tour-detail__info {
        font-size: 0.875rem;
    }

    .back-button {
        padding: 0.5rem 1.5rem;
    }
}