/* General Styles for ManageUsers */
.ManageUsers {
    padding: 2rem;
    max-width: 1000px;
    /* Adjust as necessary for your layout */
    margin: 2rem auto;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.ManageUsers h2 {
    color: #32325d;
    text-align: center;
    margin-bottom: 1.5rem;
}

/* Styles for Table */
.ManageUsers table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.ManageUsers th,
.ManageUsers td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

.ManageUsers th {
    background-color: #f4f4f4;
    color: #333;
    font-weight: bold;
}

.ManageUsers tr:nth-child(even) {
    background-color: #f9f9f9;
}

.ManageUsers tr:hover {
    background-color: #f1f1f1;
}

/* Styles for Buttons and Links */
.ManageUsers .btn {
    padding: 8px 16px;
    text-decoration: none;
    display: inline-block;
    border-radius: 4px;
    transition: background-color 0.3s ease;
    margin-right: 8px;
    /* Spacing between buttons */
}

.ManageUsers .secondary__btn {
    background-color: #3498db;
    color: white;
    border: none;
    cursor: pointer;
}

.ManageUsers .secondary__btn:hover {
    background-color: #2980b9;
}

/* Styles for Loading and No-Data Messages */
.ManageUsers .loading,
.ManageUsers .no-data {
    text-align: center;
    color: #666;
    font-size: 1rem;
    padding: 2rem;
}

/* Colores actualizados para los botones */
.ManageUsers .edit-btn {
    background-color: #f1c40f;
    /* color amarillo para editar */
}

.ManageUsers .delete-btn {
    background-color: #e74c3c;
    /* color rojo para eliminar */
}

.ManageUsers .add-btn {
    background-color: #2ecc71;
    /* color verde para añadir */
}

.ManageUsers .btn:hover {
    opacity: 0.8;
}


/* Media Queries for Responsiveness */
@media (max-width: 768px) {
    .ManageUsers table {
        display: block;
        overflow-x: auto;
        white-space: nowrap;
    }

    .ManageUsers th,
    .ManageUsers td {
        padding: 8px;
    }
}