/* General Styles for ManageSubscribes */
.ManageSubscribes {
    padding: 2rem;
    max-width: 600px;
    /* Adjust width as needed for email listing */
    margin: 2rem auto;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.ManageSubscribes h2 {
    color: #32325d;
    text-align: center;
    margin-bottom: 1.5rem;
}

/* Styles for Tables */
.ManageSubscribes table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.ManageSubscribes th,
.ManageSubscribes td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

.ManageSubscribes th {
    background-color: #f4f4f4;
    color: #333;
    font-weight: bold;
}

.ManageSubscribes tr:nth-child(even) {
    background-color: #f9f9f9;
}

.ManageSubscribes tr:hover {
    background-color: #f1f1f1;
}

/* Styles for Loading and No-Data Messages */
.ManageSubscribes .loading,
.ManageSubscribes .no-data {
    text-align: center;
    color: #666;
    font-size: 1rem;
    padding: 2rem;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
    .ManageSubscribes table {
        display: block;
        overflow-x: auto;
        white-space: nowrap;
    }

    .ManageSubscribes th,
    .ManageSubscribes td {
        padding: 8px;
    }
}