/* General Styles for ManageBookings */
.ManageBookings {
    padding: 2rem;
    max-width: 1000px;
    margin: 2rem auto;
}

.ManageBookings h2 {
    color: #32325d;
    text-align: center;
    margin-bottom: 1rem;
}

.ManageBookings p {
    text-align: center;
    color: #666;
    margin-bottom: 2rem;
}

/* Calendar Styles */
.ManageBookings .rbc-calendar {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Event Style Getter */
.ManageBookings .rbc-event {
    background-color: #3174ad;
    border-radius: 5px;
    border: 1px solid #25587a;
    color: white;
    padding: 4px 8px;
}

.ManageBookings .rbc-event:hover {
    background-color: #25587a;
}

/* Custom Modal Styles */
.ManageBookings .CustomModal {
    /* Add custom styles for your modal if necessary */
}

/* Responsive styles */
@media (max-width: 768px) {
    .ManageBookings .rbc-calendar {
        margin: 10px;
    }
}