/* General Styles for ManageReviews */
.ManageReviews {
    padding: 2rem;
    max-width: 1000px;
    margin: 2rem auto;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.ManageReviews h2 {
    color: #32325d;
    text-align: center;
    margin-bottom: 1.5rem;
}

/* Styles for Table */
.ManageReviews table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.ManageReviews th,
.ManageReviews td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

.ManageReviews th {
    background-color: #f4f4f4;
    color: #333;
    font-weight: bold;
}

.ManageReviews tr:nth-child(even) {
    background-color: #f9f9f9;
}

.ManageReviews tr:hover {
    background-color: #f1f1f1;
}

/* Styles for Buttons and Links */
.ManageReviews .delete-review {
    padding: 8px 16px;
    background-color: #e74c3c;
    color: white;
    text-decoration: none;
    border-radius: 4px;
    transition: background-color 0.3s ease;
    cursor: pointer;
}

.ManageReviews .delete-review:hover {
    background-color: #c0392b;
}

/* Styles for Loading and No-Data Messages */
.ManageReviews .loading,
.ManageReviews .no-data {
    text-align: center;
    color: #666;
    font-size: 1rem;
    padding: 2rem;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
    .ManageReviews table {
        display: block;
        overflow-x: auto;
        white-space: nowrap;
    }

    .ManageReviews th,
    .ManageReviews td {
        padding: 8px;
    }
}